import React, { useRef, useState, useCallback } from 'react';

const InteractiveLink = (props: {
  children: React.ReactNode;
  speed?: number;
  imageSrc: string;
  imageProps?: Record<any, any>;
  imageCentered?: boolean;
}) => {
  const [showImage, setShowImage] = useState(false);
  const [position, setPosition] = useState([0, 0]);
  const imgRef = useRef<HTMLImageElement>();

  const onMouseEnter = useCallback(() => setShowImage(true), [setShowImage]);
  const onMouseLeave = useCallback(() => setShowImage(false), [setShowImage]);
  const onMouseMove = useCallback(e => setPosition([e.clientX, e.clientY]), [setPosition]);

  const speed = props.speed ?? 0.1;

  return (
    <div {...{ onMouseEnter, onMouseLeave, onMouseMove }}>
      <img
        ref={imgRef}
        className="interactive-links-image"
        src={props.imageSrc}
        alt=""
        style={{
          position: 'fixed',
          display: 'block',
          opacity: showImage ? 1 : 0,
          transform: `translate(${position[0] * speed}px, ${position[1] * speed}px)`,
          ...(props.imageCentered && {
            marginTop: -imgRef.current?.clientHeight / 2,
            marginLeft: -imgRef.current?.clientWidth / 2,
          }),
        }}
        {...props.imageProps}
      />
      {props.children}
    </div>
  );
};

export default InteractiveLink;
