import React from 'react';
import classNames from 'classnames';

export const SingleImage = (props: { src: string; title?: string; className?: string }) => {
  return (
    <div
      className="row justify-content-center"
      data-aos="fade-up"
      data-aos-delay="100"
      data-aos-duration="700"
      data-aos-easing="ease-in-out"
    >
      <div className={classNames('col-12', props.className)}>
        <div className="fullscreen">
          <img src={props.src} alt="" />
          {props.title && <p className="text-gray text-center">{props.title}</p>}
        </div>
      </div>
    </div>
  );
};

export const DoubleImage = (props: { src1: string; src2: string }) => {
  return (
    <div className="row justify-content-center">
      <div
        className="row gh-1 gv-1"
        data-aos="fade-up"
        data-aos-delay="100"
        data-aos-duration="700"
        data-aos-easing="ease-in-out"
      >
        <div className="col-12 col-sm-6">
          <div className="fullscreen">
            <img src={props.src1} alt="" />
          </div>
        </div>
        <div className="col-12 col-sm-6">
          <div className="fullscreen">
            <img src={props.src2} alt="" />
          </div>
        </div>
      </div>
    </div>
  );
};

