type TextRowTitleStyle = 'default' | 'blockquote' | 'large';
const TextRowTitle = (props: { title: string; style: TextRowTitleStyle }) => {
  if (props.style === 'default') {
    return (
      <p
        className="text-gray"
        style={{ textTransform: 'uppercase' }}
        data-aos="fade-up"
        data-aos-delay="100"
        data-aos-duration="700"
        data-aos-easing="ease-in-out"
      >
        {props.title}
      </p>
    );
  }

  if (props.style === 'blockquote') {
    return (
      <blockquote
        className="text-gray"
        data-aos="fade-up"
        data-aos-delay="100"
        data-aos-duration="700"
        data-aos-easing="ease-in-out"
      >
        {props.title}
      </blockquote>
    );
  }

  if (props.style === 'large') {
    return (
      <h3 data-aos="fade-up" data-aos-delay="100" data-aos-duration="700" data-aos-easing="ease-in-out">
        {props.title}
      </h3>
    );
  }
};

export const TextRow = (props: { title?: string; titleStyle?: TextRowTitleStyle; children: React.ReactNode }) => {
  return (
    <div className="row justify-content-center">
      <div className="col-12 col-lg-10 pb-60 text-white">
        <div className="row mt-100">
          <div className="col-12 col-lg mr-lg-60 mr-xl-100">
            {props.title && <TextRowTitle title={props.title} style={props.titleStyle ?? 'default'} />}
          </div>
          <div className="col-12 col-lg-6">{props.children}</div>
        </div>
      </div>
    </div>
  );
};

export const TextRowParagraph = (props: { children: React.ReactNode; last?: boolean; lead?: boolean }) => {
  return (
    <p
      data-aos="fade-up"
      data-aos-delay="100"
      data-aos-duration="700"
      data-aos-easing="ease-in-out"
      className={[props.last ? '' : 'mb-20', props.lead ? 'lead' : ''].join(' ')}
    >
      {props.children}
    </p>
  );
};

export const TextRowList = (props: { items?: React.ReactNode[]; children?: React.ReactNode[]; ordered?: boolean }) => {
  const items = props.items ?? props.children;
  if (props.ordered) {
    return (
      <ol data-aos="fade-up" data-aos-delay="100" data-aos-duration="700" data-aos-easing="ease-in-out">
        {items?.map((item, i) => (
          <li key={i}>{item}</li>
        ))}
      </ol>
    );
  } else {
    return (
      <ul data-aos="fade-up" data-aos-delay="100" data-aos-duration="700" data-aos-easing="ease-in-out">
        {items?.map((item, i) => (
          <li key={i}>{item}</li>
        ))}
      </ul>
    );
  }
};

